<template>
  <div class="container">
    <h1>接收参数的子组件</h1>
    <div>父组件传过来的值--------{{aaa.content}}</div>
    <hr />
    <h1>子组件给父组件传参</h1>

    <div @click="postparms()">点击给父组件传参</div>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  props: {
    // 接收父组件传递的值
    aaa: {}
  },
  methods: {
    postparms() {
      // 子组件给父组件传值
      this.$emit("postparms", 1912381023);
    }
  }
};
</script>

